body {
  font-family: $font-family;
  background: $grayish-blue;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  overflow: hidden;
  width: 375px;
 box-shadow: 0px 0px 0px 4px rgba(0,0,0,0.05);
  
}

.join__header {
    background-color: $light-gray;
  padding-left: 30px;
}

.monthly {
  background-color: $cyan;
  padding-left: 30px;
}

.why {
  background-color: $cyan;
  padding-left: 30px;
}

.join__title {
  color: $cyan;
  font-size: 30px;
  margin-top: 30px;
}

.join__subtitle {
  color: $bright-yellow;
}

.join__paragraph {
  color: $grayish-blue;
}

.join__paragraph--2 {
  padding-bottom: 30px;
}

.monthly__title {
  color: #fff;
  margin-top: 30px;
}

.monthly__currency {
  color: #fff;
  font-size: 40px;
  display: inline-block;
  margin-right: 20px;
}

.monthly__paragraph1 {
  color: $light-gray;
}

.monthly__paragraph2 {
  color: $light-gray;
}

.btn__signup {
  background-color: $bright-yellow;
  font-weight: 700;
  font-size: 17px;
  color: #fff;
  border: none;
  border-radius: 5px;
  width: 90%;
  padding: 20px;
  margin-bottom: 30px;
}

.why__title {
  color: #fff;
  margin-top: 30px;
}

.why__list {
  list-style: none;
  padding-left: 0;
  line-height: 20px;
  margin-bottom: 30px;
}

.why__list {
  color: $light-gray;
}
